@import url(https://fonts.googleapis.com/css?family=Abel);

* {
  box-sizing: border-box; }

html {
  font-size: 18px; }

body,
ul,
ol,
h1,
h2,
h3,
h4,
p {
  margin: 0;
  padding: 0; }

ul,
ol {
  list-style: none; }

a {
  text-decoration: none; }

input,
button {
  outline: none; }

.page {
  width: 100vw;
  min-height: 100%; }
  @media (max-width: 1099.9px) {
    .page {
      height: 800px; } }
  @media (max-width: 767.9px) {
    .page {
      height: 1400px; } }
  @media (max-width: 574.9px) {
    .page {
      height: 1400px; } }

.lists {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  width: 80%;
  height: auto;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%); }
  @media (max-width: 767.9px) {
    .lists {
      -webkit-flex-direction: column;
              flex-direction: column;
      -webkit-align-items: center;
              align-items: center;
      position: relative;
      width: 100%; } }
  @media (max-width: 1099.9px) {
    .lists {
      width: 95%; } }

body {
  height: 100%;
  width: 100%; }

#root {
  width: 100%;
  height: 100%; }

.expandItem {
  cursor: pointer; }

#new {
  color: green; }

#absent {
  color: red; }

#modified {
  color: #e0bf00; }

#unchanged {
  color: black; }

#listBlock {
  box-shadow: 0px 0px 11px 0px rgba(0, 0, 0, 0.75);
  background-color: #fbfbfb;
  border-radius: 10px;
  padding: 40px 5px 20px 60px;
  max-width: 560px;
  height: 660px;
  font-size: 14px;
  width: 560px;
  overflow-y: scroll;
  margin-left: 10px;
  margin-right: 10px;
  font-size: 16px;
  font-weight: 600;
  font-family: 'Abel', sans-serif;
  letter-spacing: 1px; }
  @media (max-width: 767.9px) {
    #listBlock {
      margin-bottom: 20px;
      width: 100%;
      margin-left: 0;
      margin-right: 0;
      padding: 40px 5px 20px 40px; } }
  @media (max-width: 1099.9px) {
    #listBlock {
      width: 95%;
      font-size: 14px; } }

.listItem {
  cursor: pointer;
  padding: 3px 5px;
  border-radius: 5px; }

.listItem:hover {
  background-color: #e9e9e9; }

#adress {
  color: black; }

